#container {
  padding: 0;
}

.banner {
  border: 0;
  background: rgba(255, 255, 255, 0.9);
  text-align: center;
  transition: all 0.5s;
  box-shadow: 0;
  margin-top: 35px;

  img.logo {
    margin-top: 25px;
    max-height: 140px;
    -webkit-transition: all 0.4s ease;
    transition: all 0.4s ease;
  }

  .navbar-nav {
    margin-top: 0;
    a {
      padding: 20px;
    }
  }

  .navbar-nav > li {
    margin-left: -10px;
  }

  .navbar-default {
    .navbar-nav > li > a {
      font-size: 13px;
    }
  }

  &.shrink {
    background: white;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
    transition: all 0.5s;
    margin-top: 0px;
    img.logo {
      margin: 5px 0;
      max-height: 45px;
    }
    .navbar-nav > li > a {
      padding-top: 8px;
      padding-bottom: 8px;
    }
  }
}

.tp-caption {
  &.large_bold_black {
    padding: 10px 15px;
    background-color: #ffff64;
    font-size: 2.67em;
    line-height: 1.1;
  }
  &.medium_thin_black {
    background-color: #ffff64;
    padding: 5px 15px;
    font-size: 1.2em;
  }
}

.page-banner > div {
  margin-top: 185px;
}

/*
* header top-bar css
*/
.header-top-bar-container {
  background-color: rgb(0, 111, 55);
  max-height: 35px;
  .header-toggle {
    display: none;
    clear: both;
    text-align: center;
    color: #777;
    padding: 15px 0;

    -moz-transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    transform: rotate(180deg);
    -ms-filter: fliph;
    filter: fliph;

    .active {
      -moz-transform: rotate(0);
      -webkit-transform: rotate(0);
      -o-transform: rotate(0);
      transform: rotate(0);
    }

    &:hover {
      color: #000;
    }
  }
}

.header-top-bar {
  width: 1170px;
  margin-left: auto;
  margin-right: auto;
  .contact-details {
    float: left;
  }
}

.contact-details {
  li {
    float: left;
    border-left: 1px solid #E2E6E7;
    /*font-family: 'Open Sans';*/
    font-weight: 500;
    /*font-size: 13px;*/
    color: #ffffff;
    /*line-height: 20px;*/
    padding: 10px 20px;
    list-style: none;
    a {
      color: #ffffff;
    }
    :before {
      color: #A4AAB3;
      font-size: 14px;
      margin-right: 10px;
      padding-bottom: 1px;
    }
  }
}

/*
* slider
*/
.slider2 {
  margin-top: 215px;
  &.shrink {
    margin-top: 49px;
  }
}

.result-area {
  background: #f9fafb !important;
  button {
    position: relative;
  }
}

.admission-open .title-section{
  background: #ffffff !important;

}

.text-bold {
  font-weight: bold !important;
}

.page-banner {
  margin-top: 215px;
  padding: 2px 0 47px;
  &.shrink {
    margin-top: 50px;
  }
  :after {
    background: rgba(58, 61, 65, 0.62);
  }
}

.stat {
  padding: 20px 5px;
  background: #336699;
  text-align: center;
  float: left;
  margin-left: 100px;
  width: 150px;
}

.stat-count {
  font-size: 51px;
  font-weight: normal;
  letter-spacing: -0.02em;
  line-height: 1.2;
  margin-bottom: 20px;
  overflow: hidden;
  font-family: "Georgia", Courier, monospace;
  padding: 0;
  position: relative;
  color: white;
}

.stat-detail {
  color: #fff;
  padding-top: 10px;
  font: italic 1.3rem/1.75 "Georgia", Courier, monospace;
}


